@import "variables.scss";

.main-nav {
  position: relative;
  display: block;
  width: $mobile-wrapper;

  font-family: $Oswald;
  font-weight: 500;
  font-size: 20px;
  line-height: 20px;
}

.main-nav__toggle {
  position: absolute;
  top: -55px;
  right: 0;
  display: block;
  width: 24px;
  height: 16px;
  padding: 20px;
  padding-right: 0;

  background-image: url("../img/burger-menu.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: 24px 18px;
  background-color: transparent;

  border: none;
  cursor: pointer;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  &--hidden {
    display: none;
  }
}

.main-nav__wrapper {
  position: relative;
  display: block;
  width: 100%;

  &--closed {
    display: none;
  }

  &::before {
    content: "";
    position: absolute;
    display: block;
    width: 320px;
    left: -20px;
    height: 100%;
    background-color: #ffffff;
  }
}

.main-nav__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-transform: uppercase;
  width: 100%;
}

.main-nav__item {
  position: relative;
  color: #000000;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: -20px;
    display: block;
    width: 320px;
    height: 1px;
    background-color: #e6e6e6;
  }
}

.main-nav__link {
  display: block;
  padding-top: 22px;
  padding-bottom: 24px;
  color: inherit;
  text-decoration: none;
  text-align: center;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: $tablet-width) {
  .main-nav {
    width: 500px;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
  }

  .main-nav__wrapper {
    &::before {
      display: none;
    }
  }

  .main-nav__list {
    flex-direction: row;
    justify-content: flex-end;
  }

  .main-nav__item {
    margin-right: 35px;

    &:last-child {
      margin-right: 0;
    }

    &::before {
      display: none;
    }

    &--current::after {
      content: "";
      position: absolute;
      bottom: 16px;
      display: block;
      width: 100%;
      height: 2px;
      background-color: $base-green;
    }
  }
}


@media (min-width: $desktop-width) {
  .main-nav {
    width: 610px;
    font-weight: 400;
    font-size: 20px;
    line-height: 30px;
  }

  .main-nav__item--index {
    color: #ffffff;
  }

  .main-nav__item--index.main-nav__item--current::after {
    background-color: #ffffff;
  }
}
